@import "variables";
@import "resets";

body {
    background-attachment: fixed;
    background: url('/assets/images/bg3.jpg') no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}
body::after {
    content: "";
    background: #fff;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    opacity: .2;
    /* background-attachment: fixed; */
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: fixed;
    z-index: -1;
}

.global-wrapper {
	position: absolute;
	@include trbl0();

    .main-content-wrapper {
        position: absolute;
        max-width: $APP_WIDTH;
        @include center-margin();
        top: 40px;
        left: 0px;
        right: 0px;
        bottom: 0px;
        background: #fff;
        overflow-y: scroll; 
    }
}
